import React, { Fragment } from "react";
import Routes from "./Router";
import Paper from "@material-ui/core/Paper";
import AppBarComponent from "./sharedComponents/AppBarComponent";
import Box from "@material-ui/core/Box";
import AppContext from "./libs/contextLibs";
import { Auth } from "aws-amplify";

import "./App.css";

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isAuthenticated: false,
      isAuthenticating: true,
    };
  }

  componentDidMount() {
    this.onLoad();
  }

  userHasAuthenticated = (authenticated) => {
    this.setState({
      isAuthenticated: authenticated,
    });
  };

  onLoad = async () => {
    try {
      await Auth.currentSession();
      this.userHasAuthenticated(true);
      console.log("onLoad success");
    } catch (e) {
      if (e !== "No current user") {
        alert(e);
        this.userHasAuthenticated(false);
      }
    }

    this.setState({
      isAuthenticating: false,
    });
  };

  render() {
    return (
      !this.state.isAuthenticating && (
        <Box>
          <AppContext.Provider
            value={{
              isAuthenticated: this.state.isAuthenticated,
              userHasAuthenticated: this.userHasAuthenticated,
            }}
          >
            {this.state.isAuthenticated ? (
              <Fragment>
                <AppBarComponent />
                <Box className="root-container">
                  <Paper elevation={3} className="root-paper">
                    <Routes />
                  </Paper>
                </Box>
              </Fragment>
            ) : (
              <Box className="sign-in-root-container">
                <Paper elevation={3} className="sign-in-root-paper">
                  <Routes />
                </Paper>
              </Box>
            )}
          </AppContext.Provider>
        </Box>
      )
    );
  }
}

export default App;
