import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import { Link } from "react-router-dom";
import { GO_TO_CREATE_MENU } from "../../utils/Strings";

class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    return (
      <div>
        <Button
          variant="contained"
          color="primary"
          component={Link}
          to="/menuManagement"
        >
          {GO_TO_CREATE_MENU}
        </Button>
      </div>
    );
  }
}

export default Home;
