import React, { Component } from "react";
import { withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
// import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
// import IconButton from '@material-ui/core/IconButton';
// import MenuIcon from '@material-ui/icons/Menu';
import AppContext from "../libs/contextLibs";
import { Auth } from "aws-amplify";
import { Link } from "react-router-dom";
import { Constants } from "../utils/Constants";
import { LOGOUT, RESTSOFT_ADMIN_PANEL } from "../utils/Strings";

const styles = (theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
});

class AppBarComponent extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  handleLogOut = async () => {
    const { userHasAuthenticated } = this.context;
    await Auth.signOut();
    userHasAuthenticated(false);
  };

  render() {
    const { isAuthenticated } = this.context;
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
            {/*<IconButton*/}
            {/*    edge="start"*/}
            {/*    className={classes.menuButton}*/}
            {/*    color="inherit"*/}
            {/*    aria-label="menu"*/}
            {/*>*/}
            {/*    <MenuIcon/>*/}
            {/*</IconButton>*/}
            <Button component={Link} to={Constants.LINKS.HOME} color="inherit">
              {RESTSOFT_ADMIN_PANEL}
            </Button>
            <div className={classes.title}></div>
            {isAuthenticated ? (
              <Button color="inherit" onClick={this.handleLogOut}>
                {LOGOUT}
              </Button>
            ) : null}
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

AppBarComponent.contextType = AppContext;

export default withStyles(styles, { withTheme: true })(AppBarComponent);
