import { API } from "aws-amplify";
import { Constants } from "../utils/Constants";

export const createMenuApi = (params) => {
  return API.post("webadmin", Constants.ENDPOINTS.createMenuApi, params);
};
export const getMenuApi = (params) => {
  return API.post("webadmin", Constants.ENDPOINTS.getMenuApi, params);
};
export const deleteMenuApi = (params) => {
  return API.post("webadmin", Constants.ENDPOINTS.deleteMenuApi, params);
};
