import React, { Component } from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Delete } from "@material-ui/icons";
import "./menuManagement.css";
import {
  CATEGORY,
  DELETE_CONFIRMATION_BODY,
  DELETE_CONFIRMATION_TITLE,
  MENU_CODE,
  NAME,
  PRICE,
} from "../../utils/Strings";
import { deleteMenuApi } from "../../api/LambdaApi";
import IconButton from "@material-ui/core/IconButton";
import SimpleConfirmationDialog from "../../sharedComponents/SimpleConfirmationDialog";
import PropTypes from "prop-types";

class MenuListViewComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpenDeleteConfirmationDialog: false,
      selectedMenuCodeForDeletion: "",
    };
  }

  deleteMenuItem = async () => {
    console.log(
      "menuCode should be deleted",
      this.state.selectedMenuCodeForDeletion
    );
    const params = {
      body: {
        menu_code: this.state.selectedMenuCodeForDeletion,
      },
    };

    try {
      await deleteMenuApi(params);
      this.setState({
        selectedMenuCodeForDeletion: "",
      });
      this.props.shouldUpdateMenuItems();
    } catch (e) {
      alert(e);
    }
  };

  closeDeleteDialog = () => {
    this.setState({
      isOpenDeleteConfirmationDialog: false,
    });
  };

  handleDelete = (menu_code) => {
    this.setState({
      isOpenDeleteConfirmationDialog: true,
      selectedMenuCodeForDeletion: menu_code,
    });
  };

  simpleTable() {
    return (
      <div>
        <TableContainer component={Paper} className="table-max-height">
          <Table stickyHeader aria-label="sticky table">
            <TableHead className="table-head">
              <TableRow>
                <TableCell>{MENU_CODE}</TableCell>
                <TableCell align="center">{NAME}</TableCell>
                <TableCell align="center">{CATEGORY}</TableCell>
                <TableCell align="right">{PRICE}</TableCell>
                <TableCell align="center"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {this.props.menuItems.map((row, index) => (
                <TableRow key={index}>
                  <TableCell align="left"> {row.menu_code} </TableCell>
                  <TableCell align="center">{row.name}</TableCell>
                  <TableCell align="center">{row.category}</TableCell>
                  <TableCell align="right">{row.price}</TableCell>
                  <TableCell align="center">
                    <IconButton
                      aria-label="delete"
                      component="span"
                      onClick={() => {
                        this.handleDelete(row.menu_code);
                      }}
                    >
                      <Delete />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <SimpleConfirmationDialog
          open={this.state.isOpenDeleteConfirmationDialog}
          handleClose={this.closeDeleteDialog}
          title={DELETE_CONFIRMATION_TITLE}
          body={DELETE_CONFIRMATION_BODY}
          onConfirm={this.deleteMenuItem}
        />
      </div>
    );
  }

  render() {
    return <div>{this.simpleTable()}</div>;
  }
}

MenuListViewComponent.propTypes = {
  menuItems: PropTypes.array.isRequired,
  shouldUpdateMenuItems: PropTypes.func.isRequired,
};

export default MenuListViewComponent;
