import React, { Component, Fragment } from "react";
// import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import MenuListViewComponent from "./MenuListViewComponent";
import MenuEntryFormComponent from "./MenuEntryFormComponent";
import "./menuManagement.css";
import { getMenuApi } from "../../api/LambdaApi";

class MenuManagement extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menuItems: [],
    };
  }

  componentDidMount() {
    this.getMenuItems();
  }

  getMenuItems = async () => {
    const params = {};
    try {
      const result = await getMenuApi(params);
      console.log("menuItems", result);
      this.setState({
        menuItems: result,
      });
    } catch (e) {
      alert(e);
    }

    // const dummyData = [
    //   this.createData("01", "Burger", "FastFood", 3.5),
    //   this.createData("01", "Burger", "FastFood", 3.5),
    //   this.createData("01", "Burger", "FastFood", 3.5),
    // ];
    //
    // this.setState({
    //   menuItems: dummyData,
    // });
  };

  createData(menu_code, name, category, price) {
    return { menu_code, name, category, price };
  }

  shouldUpdateMenuItems = () => {
    this.getMenuItems();
  };

  render() {
    return (
      <Fragment>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
          spacing={1}
          className="full-height"
        >
          <Grid Item xs={5}>
            <Container className="basic-margin-padding">
              <MenuEntryFormComponent
                shouldUpdateMenuItems={this.shouldUpdateMenuItems}
              />
            </Container>
          </Grid>
          <Grid Item xs={7}>
            <Container className="basic-margin-padding">
              <MenuListViewComponent
                menuItems={this.state.menuItems}
                shouldUpdateMenuItems={this.shouldUpdateMenuItems}
              />
            </Container>
          </Grid>
        </Grid>
      </Fragment>
    );
  }
}

export default MenuManagement;
