import React, { Component } from "react";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import {
  ADD_MENU_ITEM,
  CATEGORY,
  MENU_CODE,
  NAME,
  PRICE,
} from "../../utils/Strings";
import { createMenuApi } from "../../api/LambdaApi";
import PropTypes from "prop-types";

class MenuEntryFormComponent extends Component {
  formInputNames = {
    menuCode: "menuCode",
    name: "name",
    category: "category",
    price: "price",
  };

  constructor(props) {
    super(props);
    Object.freeze(this.formInputNames);

    this.state = {
      menuCode: "",
      name: "",
      category: "",
      price: 0,
    };
  }

  onChangeInput(event, inputName) {
    switch (inputName) {
      case this.formInputNames.menuCode:
        this.setState({
          menuCode: event.target.value,
        });
        break;
      case this.formInputNames.name:
        this.setState({
          name: event.target.value,
        });
        break;
      case this.formInputNames.category:
        this.setState({
          category: event.target.value,
        });
        break;
      case this.formInputNames.price:
        this.setState({
          price: event.target.value,
        });
        break;
      default:
        return;
    }
  }

  submitForm = async () => {
    const params = {
      body: {
        name: this.state.name,
        menu_code: this.state.menuCode,
        price: this.state.price,
        category: this.state.category,
      },
    };
    try {
      await createMenuApi(params);
      this.props.shouldUpdateMenuItems();
      this.setState({
        menuCode: "",
      });
    } catch (error) {
      console.log("error", error);
    }
  };

  isButtonEnabled() {
    if (
      this.state.menuCode !== "" &&
      this.state.name !== "" &&
      this.state.price !== 0
    ) {
      return false;
    }
    return true;
  }

  render() {
    return (
      <div>
        <div className="menu-item-header">{ADD_MENU_ITEM}</div>
        <form noValidate autoComplete="off">
          <Grid container direction="column" justify="space-evenly">
            <Grid Item>
              <TextField
                className="text-field"
                required
                id="standard-basic"
                label={MENU_CODE}
                value={this.state.menuCode}
                onChange={(event) =>
                  this.onChangeInput(event, this.formInputNames.menuCode)
                }
              />
            </Grid>
            <Grid Item>
              <TextField
                className="text-field"
                required
                id="standard-basic"
                label={NAME}
                onChange={(event) =>
                  this.onChangeInput(event, this.formInputNames.name)
                }
              />
            </Grid>
            <Grid Item>
              <TextField
                className="text-field"
                id="standard-basic"
                label={CATEGORY}
                onChange={(event) =>
                  this.onChangeInput(event, this.formInputNames.category)
                }
              />
            </Grid>
            <Grid Item>
              <TextField
                className="text-field"
                required
                id="standard-basic"
                label={PRICE}
                type="number"
                onChange={(event) =>
                  this.onChangeInput(event, this.formInputNames.price)
                }
              />
            </Grid>
            <Grid Item>
              <div className="add-menu-button">
                <Button
                  variant="contained"
                  color="primary"
                  onClick={this.submitForm}
                  disabled={this.isButtonEnabled()}
                >
                  {ADD_MENU_ITEM}
                </Button>
              </div>
            </Grid>
          </Grid>
        </form>
      </div>
    );
  }
}

MenuEntryFormComponent.propTypes = {
  shouldUpdateMenuItems: PropTypes.func.isRequired,
};

export default MenuEntryFormComponent;
